import "./globals.css";
import type { Metadata } from "next";
import AuthGate from "@/components/AuthGate";
import clsx from "clsx";

export const metadata: Metadata = {
  title: "Expense Tracker",
  description: "Track expenses with insights (Firebase + Next.js)"
};

export default function RootLayout({ children }: { children: React.ReactNode }) {
  return (
    <html lang="en" suppressHydrationWarning>
      <body className={clsx("min-h-screen bg-white text-black dark:bg-neutral-950 dark:text-white")}>
        <AuthGate>
          <div className="max-w-3xl mx-auto p-4 space-y-6">{children}</div>
        </AuthGate>
      </body>
    </html>
  );
}
